<template>
  <div class="drop-body" :class="classes">
    <slot></slot>
  </div>
</template>

<script>

export default {
  name: 'drop',
  props: {
    show: {
      required: false,
      type: Boolean,
      default: false
    },
    autoColse: {
      required: false,
      type: Boolean,
      default: true
    },
    direction: {
      required: false,
      type: String,
      default: 'down'
    }
  },
  data () {
    return {
      dropdownFlag: true,
      directionMap: {
        left: 'drop-left',
        right: 'drop-right',
        up: 'drop-up',
        down: 'drop-down'
      },
      shownClassName: 'show-drop'
    }
  },
  mounted () {
  },
  computed: {
    styleClass () {
      return this.directionMap[this.direction] || 'drop-down'
    },
    classes () {
      let classes = [this.styleClass]
      return this.show ? classes.concat([this.shownClassName]) : classes
    }
  },
  methods: {
    hideDropdown () {
      this.$emit('update:show', false)
    },
    docEventHideDropDown () {
      this.hideDropdown()
      // 可能是 vue 更新了数据传递机制，之前 show 变为 true 后，会先绑定事件，然后事件冒泡到 document，
      // 触发绑定的回调，所以预制一个开关变量应对这种情况。
      // 目前是 document 会先接收到第一次点击事件的冒泡，之后 show 变为 true 绑定 addDocEventListener
      // 所以不再需要 dropdownFlag
      // if (!this.dropdownFlag) {
      //   this.hideDropdown()
      // } else {
      //   this.dropdownFlag = false
      // }
    },
    addDocEventListener () {
      document.addEventListener('click', this.docEventHideDropDown)
    },
    removeDocEventListener () {
      document.removeEventListener('click', this.docEventHideDropDown)
    }
  },
  watch: {
    show (val, oldVal) {
      if (this.autoColse) {
        if (val) {
          // this.dropdownFlag = true
          this.addDocEventListener()
        } else {
          this.removeDocEventListener()
        }
      }
    }
  }
}
</script>

<style src="./drop.less" lang="less"></style>



