<template>
  <div class="calendar-container" v-show="dateValidator()">
    <div class="calendar-header-container">
      <v-button type="text" size="small" :icon="true" class="calendar-header-btn" @click.native="changeYear(-1)" :title="$t('time.prevYear')"><i class="icon-angle-double-left"></i></v-button>
      <v-button type="text" size="small" :icon="true" class="calendar-header-btn" @click.native="changeMonth(-1)" :title="$t('time.prevMonth')"><i class="icon-angle-left"></i></v-button>
      <div class="calendar-header">{{ calendarMonthHeader }}</div>
      <v-button type="text" size="small" :icon="true" class="calendar-header-btn" @click.native="changeMonth(1)" :title="$t('time.nextMonth')"><i class="icon-angle-right"></i></v-button>
      <v-button type="text" size="small" :icon="true" class="calendar-header-btn" @click.native="changeYear(1)" :title="$t('time.nextYear')"><i class="icon-angle-double-right"></i></v-button>
    </div>
    <div class="calendar-body">
      <div class="calendar-title">
        <span class="calendar-title-item" v-for="(d, index) in moment.weekdaysMin()" :key="index">
          {{ d }}
        </span>
      </div>
      <div class="calendar-items" :class="{ 'editable': !readonly }">
        <div v-for="(l, indexL) in randerList.prevMonth.arr">
          <div class="calendar-body-item" :class="{ 'selected': selectedState(randerList.prevMonth.year, randerList.prevMonth.month, indexL + randerList.prevMonth.fix, indexL + randerList.prevMonth.dayOfYear), 'range-start': rangeStartState(randerList.currentMonth.year, randerList.prevMonth.month, indexL + randerList.prevMonth.fix, indexL + randerList.prevMonth.dayOfYear) }" @click="clickCalendarItem(-1, indexL + randerList.prevMonth.fix, indexL + randerList.prevMonth.dayOfYear, (indexL + randerList.prevMonth.firstDay) % 7)">{{ indexL + randerList.prevMonth.fix }}</div>
        </div>
        <div v-for="(c, indexC) in randerList.currentMonth.arr">
          <div class="calendar-body-item current-month" :class="{ 'selected': selectedState(randerList.currentMonth.year, randerList.currentMonth.month, indexC + randerList.currentMonth.fix, indexC + randerList.currentMonth.dayOfYear), 'range-start': rangeStartState(randerList.currentMonth.year, randerList.currentMonth.month, indexC + randerList.currentMonth.fix, indexC + randerList.currentMonth.dayOfYear) }" @click="clickCalendarItem(0, indexC + randerList.currentMonth.fix, indexC + randerList.currentMonth.dayOfYear, (indexC + randerList.currentMonth.firstDay) % 7)">{{ indexC + randerList.currentMonth.fix }}</div>
        </div>
        <div v-for="(n, indexN) in randerList.nextMonth.arr">
          <div class="calendar-body-item" :class="{ 'selected': selectedState(randerList.nextMonth.year, randerList.nextMonth.month, indexN + randerList.nextMonth.fix, indexN + randerList.nextMonth.dayOfYear), 'range-start': rangeStartState(randerList.nextMonth.year, randerList.nextMonth.month, indexN + randerList.nextMonth.fix, indexN + randerList.nextMonth.dayOfYear) }" @click="clickCalendarItem(1, indexN + randerList.nextMonth.fix, indexN + randerList.nextMonth.dayOfYear, (indexN + randerList.nextMonth.firstDay) % 7)">{{ indexN + randerList.nextMonth.fix }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'moment'
import { modified } from './assets/moment-locale-zh-cn'

export default {
  name: 'calendar',
  props: {
    locale: {
      required: false,
      type: String,
      default: 'zh-cn'
    },
    currentTime: {
      required: false,
      type: [Number, String],
      default: (+new Date())
    },
    selected: {
      required: false,
      type: Object,
      default: null
    },
    readonly: {
      required: false,
      type: Boolean,
      default: false
    },
    range: {
      required: false,
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      year: null,
      month: null,
      now: null,
      calendarMonthHeader: '',
      moment: moment,
      selectedValue: 1,
      unselectedValue: 0,
      selectedRangeArr: [],
      selectTemp: {}
    }
  },
  created () {
    this.initPlugins()
    this.initDate()
  },
  mounted () {
  },
  computed: {
    randerList () {
      if (this.dateValidator()) {
        return this.calculateMonthCalendar(this.year, this.month)
      } else {
        return null
      }
    },
    rangeMode: {
      set (val) {
        this.$emit('update:range', val)
      },
      get () {
        return this.range
      }
    }
  },
  methods: {
    dateValidator () {
      return typeof this.year === 'number' && typeof this.month === 'number'
    },
    initPlugins () {
      moment.locale(this.locale)
      moment.updateLocale(this.locale, modified)
    },
    updateDate () {
      this.calendarMonthHeader = this.now.format('LL')
      this.year = this.now.year()
      this.month = this.now.month()
    },
    initDate () {
      try {
        this.now = moment(this.currentTime)
        this.updateDate()
      } catch (error) {
        console && console.error(error)
      }
    },
    changeYear (step) {
      if (step !== 0) {
        this.now.add(step, 'year')
        this.updateDate()
      }
    },
    changeMonth (step) {
      if (step !== 0) {
        this.now.add(step, 'month')
        this.updateDate()
      }
    },
    clickCalendarItem (step, date, dayOfYear, dayOfWeek) {
      if (!this.readonly) {
        this.changeMonth(step)
        if (this.rangeMode) {
          this.changeRangeSelectedState(this.year, this.month, date, dayOfYear, dayOfWeek)
        } else {
          this.chagngeSelectedState(this.year, this.month, date, dayOfYear, dayOfWeek)
        }
      }
    },
    chagngeSelectedState (year, month, date, dayOfYear, dayOfWeek) {
      // this.setSelectedDateObj(year, month, date, dayOfYear, dayOfWeek)
      this.setSelectedDateArr(year, dayOfYear, dayOfWeek, month, date)
    },
    setSelectedDateObj (year, month, date, dayOfYear, dayOfWeek) {
      var state = null
      if (this.selectedState(year, month, date)) {
        this.selected[year][month][date] = this.unselectedValue
        state = this.unselectedValue
      } else {
        state = this.selectedValue
        if (this.selected[year]) {
          if (this.selected[year][month]) {
            this.$set(this.selected[year][month], date, this.selectedValue)
          } else {
            this.$set(this.selected[year], month, { [date]: this.selectedValue })
          }
        } else {
          this.$set(this.selected, year, { [month]: { [date]: this.selectedValue } })
        }
      }
      this.selectChange({
        state,
        dayOfWeek,
        dayOfYear,
        month,
        date
      })
    },
    setSelectedDateArr (year, dayOfYear, dayOfWeek, month, date) {
      var state = null
      if (this.selectedState(year, null, null, dayOfYear)) {
        this.selected[year].splice(dayOfYear - 1, 1, this.unselectedValue)
        state = this.unselectedValue
      } else {
        state = this.selectedValue
        if (this.selected[year]) {
          if (this.selected[year][dayOfYear - 1] === undefined) {
            this.selected[year][dayOfYear - 1] = this.unselectedValue
          }
          this.selected[year].splice(dayOfYear - 1, 1, this.selectedValue)
          // var _tmp1 = this.selected[year].slice()
          // _tmp1[dayOfYear - 1] = 1
          // this.selected[year] = _tmp1
        } else {
          var _tmp2 = []
          _tmp2[dayOfYear - 1] = this.selectedValue
          this.$set(this.selected, year, _tmp2)
        }
      }
      this.selectChange({
        state,
        dayOfWeek,
        dayOfYear,
        month,
        date
      })
    },
    selectedState (year, month, date, dayOfYear) {
      // return this.selectedStateObj(year, month, date)
      return this.selectedStateArr(year, dayOfYear)
    },
    selectedStateObj (year, month, date) {
      return this.selected && this.selected[year] && this.selected[year][month] && this.selected[year][month][date] === this.selectedValue
    },
    selectedStateArr (year, dayOfYear) {
      return this.selected && this.selected[year] && this.selected[year][dayOfYear - 1] === this.selectedValue
    },
    calculateMonthCalendar (year, month) {
      var date = moment().year(year).month(month)
      var firstDate = 1
      var lastDate = date.daysInMonth()
      var firstDay = date.set('date', firstDate).day()  // 0 - 6, Sunday as 0 and Saturday as 6
      var dayOfYear = date.dayOfYear()  // 1 - 366
      var lastDay = date.set('date', lastDate).day()
      var prevMonth = date.subtract(1, 'month').daysInMonth()

      var prevMonthIndex = date.month()
      var prevYearIndex = date.year()
      var prevDayOfYear = date.dayOfYear()

      date.add(1, 'month')
      var currentMonthIndex = date.month()
      var currentYearIndex = date.year()

      date.add(1, 'month')
      var nextMonthIndex = date.month()
      var nextYearIndex = date.year()

      date = null
      if (firstDay === 0) {
        firstDay = 7
      }
      if (lastDay === 6) {
        lastDay = -1
      }
      return {
        prevMonth: {
          arr: firstDay,
          fix: prevMonth - firstDay + 1,
          month: prevMonthIndex,
          year: prevYearIndex,
          dayOfYear: prevYearIndex === currentYearIndex ? dayOfYear - firstDay : prevDayOfYear - firstDay + 1,
          // firstDay: firstDay - 1 - firstDay + 1
          firstDay: 0
        },
        currentMonth: {
          arr: lastDate,
          fix: 1,
          month: currentMonthIndex,
          year: currentYearIndex,
          dayOfYear: dayOfYear,
          firstDay: firstDay
        },
        nextMonth: {
          arr: 7 - lastDay - 1,
          fix: 1,
          month: nextMonthIndex,
          year: nextYearIndex,
          dayOfYear: nextYearIndex === currentYearIndex ? dayOfYear + lastDate : 1,
          firstDay: lastDay + 1
        }
      }
    },
    selectChange (eventData) {
      this.$emit('select-change', eventData)
    },
    rangeSelectChange () {
      this.$emit('range-change')
    },
    switchRangeMode (state) {
      this.rangeMode = state
    },
    changeRangeSelectedState (year, month, date, dayOfYear, dayOfWeek) {
      this.markRangePoint(year, month, date, dayOfYear, dayOfWeek)
      if (this.selectedRangeArr.length === 0) {
        this.setRangeStart()
      } else {
        this.setRangeEnd()
        this.switchRangeMode(false)
      }
    },
    markRangePoint (year, month, date, dayOfYear, dayOfWeek) {
      this.selectTemp = {
        year: year,
        month: month,
        date: date,
        dayOfYear: dayOfYear,
        dayOfWeek: dayOfWeek
      }
    },
    rangeStartState (year, month, date, dayOfYear) {
      if (this.rangeMode && this.selectedRangeArr[0] && this.selectedRangeArr[0].year === year && this.selectedRangeArr[0].dayOfYear === dayOfYear) {
        return true
      }
      return false
    },
    setRangeStart () {
      this.$set(this.selectedRangeArr, 0, this.selectTemp)
    },
    setRangeEnd () {
      this.$set(this.selectedRangeArr, 1, this.selectTemp)
      if (this.selectedRangeArr[0] && this.selectedRangeArr[1]) {
        if (this.selectedRangeArr[1].year < this.selectedRangeArr[0].year || (this.selectedRangeArr[1].year === this.selectedRangeArr[0].year && this.selectedRangeArr[1].dayOfYear < this.selectedRangeArr[0].dayOfYear)) {
          this.selectedRangeArr.reverse()
        }

        let newArr = this.initSelectRangeArr(this.selectedRangeArr[0].year, this.selectedRangeArr[0].dayOfYear, this.selectedRangeArr[1].year, this.selectedRangeArr[1].dayOfYear)
        this.setSelectedDateRangeArrs(newArr, this.selectedValue)
        this.rangeSelectChange()
        this.selectedRangeArr = []
      }
    },
    initSelectRangeArr (startYear, startDayOfYear, endYear, endDayOfYear) {
      var newArr = []
      if (startYear === endYear) {
        newArr.push({
          year: startYear,
          startIndex: startDayOfYear - 1,
          endIndex: endDayOfYear - 1
        })
      } else {
        newArr.push({
          year: startYear,
          startIndex: startDayOfYear - 1,
          endIndex: this.getYearDays(startYear) - 1
        })
        for (let i = startYear + 1; i <= endYear - 1; i++) {
          newArr.push({
            year: i,
            startIndex: 0,
            endIndex: this.getYearDays(i) - 1
          })
        }
        newArr.push({
          year: endYear,
          startIndex: 0,
          endIndex: endDayOfYear - 1
        })
      }
      return newArr
    },
    setSelectedDateRangeArrs (rangeArr, state) {
      for (let i = 0; i < rangeArr.length; i++) {
        var year = rangeArr[i].year
        if (!this.selected[year]) {
          this.$set(this.selected, year, [])
        }
        var _selectedDays = [].concat(this.selected[year])
        for (let dayIndex = rangeArr[i].startIndex; dayIndex <= rangeArr[i].endIndex; dayIndex++) {
          if (state === undefined) {
            if (_selectedDays[dayIndex] !== this.selectedValue) {
              _selectedDays[dayIndex] = this.selectedValue
            } else {
              _selectedDays[dayIndex] = this.unselectedValue
            }
          } else {
            _selectedDays[dayIndex] = state
          }
        }
        this.selected[year] = _selectedDays
      }
    },
    getYearDays (year) {
      if (moment([year]).isLeapYear()) {
        return 366
      } else {
        return 365
      }
      // if (((year % 4 === 0) && (year % 100 !== 0)) || (year % 400 === 0)) {
      //   return 366
      // } else {
      //   return 365
      // }
    }
  },
  watch: {
    year (val, oldVal) {
      this.$emit('year-change', val)
    },
    month (val, oldVal) {
      this.$emit('month-change', {
        month: val,
        year: this.year
      })
    }
  }
}
</script>

<style src="./calendar.less" lang="less"></style>



