<template>
  <div class="date-time-picker-container" :class="{'open-left': openLeft}">
    <div class="date-time-picker" :class="[miniMode ? 'with-mini' : '']" @click.prevent="toggleDropdown">
      <i class="icon-calendar2"></i>
      <span class="date-time-picker-label" v-if="useSelections" :title="getLabel(dateTime)">{{ getLabel(dateTime) }}</span>
      <span class="date-time-picker-label" v-if="!useSelections" :title="getRangeLabel(dateTimeRange)">{{ getRangeLabel(dateTimeRange) }}</span>
    </div>
    <div :class="{'dropdown-show': dropdownState, 'dropdown-show-mini': miniMode}" class="date-time-picker-dropdown" @click.stop.prevent="clickDropdown">
      <ul class="date-time-picker-selection-container" v-if="useSelections">
        <li v-for="(item, key) in selectionItems" :key="key" @click.stop.prevent="clickItem(selectionItems[key].params, $event)" :class="{'active': item.params.value === dateTime.value}" class="selection-item selectable">
          {{ item.label ? item.label : getLabel(item.params) }}
        </li>
        <li v-if="useCustom" @click.stop.prevent="clickCustom" :class="{'active': dateTime.type === custom.params.type, 'calendar-open': calendarState}" class="selection-item selectable">
          {{ custom.label }}
          <i class="date-time-picker-icon to-right icon-angle-right"></i>
          <i class="date-time-picker-icon to-left icon-angle-left"></i>
        </li>
        <li v-if="useRecent" @click.stop.prevent="clickRecent" :class="{ 'recent-open': recentState, 'selectable': recentList.length > 0 }"  class="selection-item">
          {{ recent.label }}
          <i class="date-time-picker-icon to-right icon-angle-right"></i>
          <i class="date-time-picker-icon to-left icon-angle-left"></i>
        </li>
      </ul>
      <div v-if="useCustom" v-show="calendarState" class="date-time-picker-dropdown-outer" ref="dateTimePickerDropdownOuter">
        <div class="date-time-picker-dropdown-frame">
          <div class="date-time-picker-dropdown-inner" ref="dateTimePickerDropdownInner" :style="{transform: countMoveSideStr}">
            <div class="date-time-picker-calendar-container">
              <div class="calendar-left-container">
                <div class="calendar-header-container">
                  <div class="calendar-item selectable" @click.stop="changeMonth('left', 'subtract', 1, $event)">
                    <i class="icon-arrow-circle-left"></i>
                  </div>
                  <div v-if="leftYear" class="calendar-header">
                    <span>{{ leftYear }}</span>年<span>{{ months[leftMonth] }}</span>
                  </div>
                  <div class="calendar-item selectable" @click.stop="changeMonth('left', 'add', 1, $event)">
                    <i class="icon-arrow-circle-right"></i>
                  </div>
                </div>
                <div class="calendar-title-container">
                  <div v-for="n in 7" :key="n" class="calendar-item">
                    {{ weekdaysMin[n - 1] }}
                  </div>
                </div>
                <div v-if="calendarLeft" class="calendar-body-container">
                  <div v-for="l in calendarLeft[0].arr" @click.stop="changeDate('left', l - 1 + calendarLeft[0].fix, $event, 'subtract')" class="calendar-item calendar-last-month selectable">
                    {{ l - 1 + calendarLeft[0].fix }}
                  </div>
                  <div v-for="c in calendarLeft[1].arr" @click.stop="changeDate('left', c - 1 + calendarLeft[1].fix, $event)" class="calendar-item selectable" :class="calendarItemClass(c - 1 + calendarLeft[1].fix, 'left')">
                    {{ c - 1 + calendarLeft[1].fix }}
                  </div>
                  <div v-for="n in calendarLeft[2].arr" @click.stop="changeDate('left', n - 1 + calendarLeft[2].fix, $event, 'add')" class="calendar-item calendar-next-month selectable">
                    {{ n - 1 + calendarLeft[2].fix }}
                  </div>
                </div>
              </div>
              <div class="calendar-right-container">
                <div class="calendar-header-container">
                  <div class="calendar-item selectable" @click.stop="changeMonth('right', 'subtract', 1, $event)">
                    <i class="icon-arrow-circle-left"></i>
                  </div>
                  <div v-if="leftYear" class="calendar-header">
                    {{ rightYear }}年{{ months[rightMonth] }}
                  </div>
                  <div class="calendar-item selectable" @click.stop="changeMonth('right', 'add', 1, $event)">
                    <i class="icon-arrow-circle-right"></i>
                  </div>
                </div>
                <div class="calendar-title-container">
                  <div v-for="n in 7" :key="n" class="calendar-item">
                    {{ weekdaysMin[n - 1] }}
                  </div>
                </div>
                <div v-if="calendarRight" class="calendar-body-container">
                  <div v-for="l in calendarRight[0].arr" @click.stop="changeDate('right', l - 1 + calendarRight[0].fix, $event, 'subtract')" class="calendar-item calendar-last-month selectable">
                    {{ l - 1 + calendarRight[0].fix }}
                  </div>
                  <div v-for="c in calendarRight[1].arr" @click.stop="changeDate('right', c - 1 + calendarRight[1].fix, $event)" class="calendar-item selectable" :class="calendarItemClass(c - 1 + calendarRight[1].fix, 'right')">
                    {{ c - 1 + calendarRight[1].fix }}
                  </div>
                  <div v-for="n in calendarRight[2].arr" @click.stop="changeDate('right', n - 1 + calendarRight[2].fix, $event, 'add')" class="calendar-item calendar-next-month selectable">
                    {{ n - 1 + calendarRight[2].fix }}
                  </div>
                </div>
              </div>
            </div>
            <div class="date-time-picker-time-container">
              <div class="time-left-container" v-if="leftDate">
                <div class="date-time-label">
                  <span>{{ leftDate.format('YYYY-MM-DD') }}</span>
                </div>
                <div class="date-time-picker-input-container">
                  <input @click.stop="clickInput('left', $event)" ref="leftInput" class="date-time-picker-input" type="text" name="leftTime" v-model="leftTime" value="">
                  <div class="time-input-dropdown" :class="{'dropdown-show': leftTimeDropdownState}" @click.stop>
                    <div class="time-input-selection-container hour-container" ref="leftHour" @mouseenter.stop="focusInput('left', 0, 2)">
                      <div class="input-selection-item-container" :class="[getTimeItemClass('left', 'hour', h - 1), {'active': isInputItemActive('left', 'hour', h - 1)}]" v-for="h in 24" :key="h" @click.stop="clickInputItem('left', 'hour', h - 1, $event)">
                        <span class="input-selection-item">{{ h - 1 | placeholder }}</span>
                      </div>
                    </div>
                    <div class="time-input-selection-container minute-container" ref="leftMinute" @mouseenter.stop="focusInput('left', 3, 5)">
                      <div class="input-selection-item-container" :class="[getTimeItemClass('left', 'minute', m - 1), {'active': isInputItemActive('left', 'minute', m - 1)}]" v-for="m in 60" :key="m" @click.stop="clickInputItem('left', 'minute', m - 1, $event)">
                        <span class="input-selection-item">{{ m - 1 | placeholder }}</span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="time-right-container" v-if="rightDate">
                <div class="date-time-label">
                  <span>{{ rightDate.format('YYYY-MM-DD') }}</span>
                </div>
                <div class="date-time-picker-input-container">
                  <input @click.stop="clickInput('right', $event)" ref="rightInput" class="date-time-picker-input" type="text" name="rightTime" v-model="rightTime" value="">
                  <div class="time-input-dropdown" :class="{'dropdown-show': rightTimeDropdownState}" @click.stop>
                    <div class="time-input-selection-container hour-container" ref="rightHour" @mouseenter.stop="focusInput('right', 0, 2)">
                      <div class="input-selection-item-container" :class="[getTimeItemClass('right', 'hour', h - 1), {'active': isInputItemActive('right', 'hour', h - 1)}]" v-for="h in 24" :key="h" @click.stop="clickInputItem('right', 'hour', h - 1, $event)">
                        <span class="input-selection-item">{{ h - 1 | placeholder }}</span>
                      </div>
                    </div>
                    <div class="time-input-selection-container minute-container" ref="rightMinute" @mouseenter.stop="focusInput('right', 3, 5)">
                      <div class="input-selection-item-container" :class="[getTimeItemClass('right', 'minute', m - 1), {'active': isInputItemActive('right', 'minute', m - 1)}]" v-for="m in 60" :key="m" @click.stop="clickInputItem('right', 'minute', m - 1, $event)">
                        <span class="input-selection-item">{{ m - 1 | placeholder }}</span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div v-show="miniMode" :title="coverRight ? '设置结束时间' : '设置开始时间'" class="move-cover" :class="[coverRight ? 'move-cover-right' : 'move-cover-left']" @click="switchCoverSide()">
              <div class="move-cover-arrow">
                <svg viewBox="0 0 34 34" width="100%" height="100%">
                    <path d="M 2 -17 l 20 34 l -20 34 " stroke-linecap="round" stroke-width="3" fill="transparent" stroke="inherit"></path>
                </svg>
              </div>
            </div>
          </div>
        </div>
        <div class="date-time-picker-confirm-container-outer">
          <div class="date-time-picker-confirm-container">
            <v-button type="primary" size="small" @click.native.stop="clickConfirm">{{ confirmLabel }}</v-button>
            <!-- <a class="btn date-time-picker-confirm-btn" @click.stop="clickConfirm">{{ confirmLabel }}</a> -->
          </div>
        </div>
      </div>
      <div v-if="useRecent" v-show="recentState">
        <ul class="date-time-picker-selection-container">
          <li v-for="(item, index) in recentList" :key="recentStrList[index]" @click.stop.prevent="clickItem(item, $event)" class="selection-item selectable">
            {{ getLabel(item) }}
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'moment'
import { isArray } from '../../lib/utils'
import defselections from './defSelections'
import { modified } from './moment-locale-zh-cn'
// import gns from '../../lib/globalNStrings'
import localStore from 'store2'
export default {
  props: {
    locale: {
      required: false,
      type: String,
      default: 'zh-cn'
    },
    selection: {
      required: false,
      type: Number,
      default: 0
    },
    useCustom: {
      required: false,
      type: Boolean,
      default: true
    },
    openLeft: {
      required: false,
      type: Boolean,
      default: false
    },
    historyLength: {
      required: false,
      type: Number,
      default: 10
    },
    useNotify: {
      required: false,
      type: Boolean,
      default: false
    },
    useRecent: {
      required: false,
      type: Boolean,
      default: true
    },
    recentLength: {
      required: false,
      type: Number,
      default: 10
    },
    dateTime: {
      required: false,
      type: Object,
      twoWay: true,
      default () {
        return {
          type: 'relative',
          value: 5 * 60
        }
      }
    },
    dateTimeRange: {
      required: false,
      twoWay: true,
      default: null
    },
    miniMode: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      miniModeMoveCount: 0,
      coverRight: true,
      dropdownState: false,
      dropdownFlag: false,
      useSelections: true,
      calendarState: false,
      leftTimeDropdownState: false,
      rightTimeDropdownState: false,
      recentState: false,
      custom: {
        params: {
          type: 'absolute'
        },
        label: this.$t('time.text.custom')
      },
      recent: {
        label: this.$t('time.text.recentUse')
      },
      confirmLabel: this.$t('time.text.confirm'),
      leftDate: null,
      rightDate: null,
      leftYear: null,
      rightYear: null,
      leftMonth: null,
      rightMonth: null,
      historyList: [],
      historyCurrentIndex: 0,
      recentList: [],
      recentStrList: [],
      regH: /^([0-9]|1[0-9]|20|21|22|23|[0-1]\d):/,
      regM: /^([0-9]|1[0-9]|20|21|22|23|[0-1]\d):([0-5]\d|[0-9])/,
      // dateTimeRecentKey: gns.localStorageKeys.dateTimeRecent
      dateTimeRecentKey: 'doc.dateTimeRecent',
      months: [],
      weekdaysMin: []
    }
  },
  computed: {
    countMoveSideStr () {
      if (this.coverRight === true) {
        return 'translateX(0)'
      } else {
        return 'translateX(-' + this.miniModeMoveCount + 'px)'
      }
    },
    selectionItems () {
      var items = defselections[this.selection] || defselections[0]
      return items
    },
    calendarLeft () {
      if (this.leftYear) {
        return this.calculateCalendar(this.leftYear, this.leftMonth)
      } else {
        return null
      }
    },
    calendarRight () {
      if (this.rightYear) {
        return this.calculateCalendar(this.rightYear, this.rightMonth)
      } else {
        return null
      }
    },
    leftTime: {
      get () {
        try {
          if (this.leftDate) {
            return this.leftDate.format('HH:mm')
          } else {
            return ''
          }
        } catch (e) {
          console && console.error(e)
        }
      },
      set (val) {
        this.checkMomentTimeFormat('left', val)
      }
    },
    rightTime: {
      get () {
        try {
          if (this.rightDate) {
            return this.rightDate.format('HH:mm')
          } else {
            return ''
          }
        } catch (e) {
          console && console.error(e)
        }
      },
      set (val) {
        this.checkMomentTimeFormat('right', val)
      }
    }
  },
  created () {
    this.initMoment()
    this.addNotify()
    if (this.selectionItems[this.dateTime.value.toString()] && this.selectionItems[this.dateTime.value.toString()].params.type !== this.dateTime.type) {
      this.setDateTime(this.selectionItems[this.dateTime.value.toString()].params)
    }
    if (isArray(this.dateTimeRange)) {
      this.useSelections = false
      this.useCustom = true
      this.initDateTimeRangeMode()
    }
  },
  mounted () {
    if (!this.useSelections) {
      this.clickCustom()
    }
    this.getRecentFromLocal()
    this.initMiniMode(this.$refs.dateTimePickerDropdownInner)
  },
  destroyed () {
    this.clearDocEvent()
  },
  watch: {
  },
  methods: {
    initMoment () {
      moment.locale(this.locale)
      moment.updateLocale(this.locale, modified)
      this.months = moment.months()
      this.weekdaysMin = moment.weekdaysMin()
    },
    getTimeItemClass (direct, method, param) {
      return `js-${direct}-${method}-${param}`
    },
    checkMomentTimeFormat (direct, str) {
      if (typeof str === 'string') {
        var hArr = str.match(this.regH)
        if (hArr) {
          var mArr = str.match(this.regM)
          if (mArr) {
            this.clickInputItem(direct, 'hour', mArr[1] - 0)
            this.clickInputItem(direct, 'minute', mArr[2] - 0)
          } else {
            this.clickInputItem(direct, 'hour', hArr[1] - 0)
          }
        } else if (str === '') {
          // var index = direct === 'left' ? 0 : 1
          // this[`${direct}Date`] = this.getNowRange(this.dateTime)[index]
        }
      }
    },
    initMiniMode (el, reset) {
      if (this.miniMode && (this.miniModeMoveCount === 0 || reset)) {
        this.$nextTick(() => {
          let halfWidth = el.clientWidth / 2
          this.miniModeMoveCount = halfWidth - (this.$refs.dateTimePickerDropdownOuter.clientWidth - halfWidth)
        })
      }
    },
    switchCoverSide () {
      this.coverRight = !this.coverRight
    },
    addNotify () {
      if (this.useNotify) {
        this.$watch('dropdownState', (val, oldVal) => {
          this.$emit('dropdown-change', this.dropdownState)
        })
      }
    },
    getLabel (params) {
      if (typeof params.value === 'number') {
        return `${this.$t('time.relative.recent')} ${moment.duration(params.value, 'seconds').humanize()}`
      } else if (isArray(params.value)) {
        this.checkValue(params.value)
        return `${this.getMoment(params.value[0]).calendar()} - ${this.getMoment(params.value[1]).calendar()}`
      } else if (typeof params.value === 'string') {
        return this.$t('time.byNow.' + params.value)
      } else {
        return this.$t('time.error.errorParams')
      }
    },
    getRangeLabel (value) {
      let _label = ''
      if (isArray(value)) {
        for (let i = 0; i < value.length; i++) {
          if (i > 0) {
            _label += ' - '
          }
          _label += this.getMoment(value[i]).calendar()
        }
      }
      if (this.miniMode === true) {
        return _label.replace(' - ', ' 至 ').replace(/年/g, '-').replace(/月/g, '-').replace(/日/g, '')
      } else {
        return _label
      }
    },
    checkValue (valArray) {
      if (valArray[0] > valArray[1]) {
        var tmp = valArray[1]
        valArray[1] = valArray[0]
        valArray[0] = tmp
      }
    },
    getMoment (unix) {
      return moment.unix(unix)
    },
    getCalendarMoment (direct) {
      return moment().year(this[`${direct}Year`]).month(this[`${direct}Month`])
    },
    getNowRange (params) {
      var range = []
      if (params.type === this.custom.params.type) {
        range[0] = this.getMoment(params.value[0])
        range[1] = this.getMoment(params.value[1])
      } else {
        var now = moment().second(0).millisecond(0)
        var copy = now.clone()
        range[0] = copy.subtract(params.value, 'seconds')
        range[1] = now
        now = null
        copy = null
      }
      return range
    },
    clickItem (dateTime, event) {
      this.setDateTime(dateTime)
      this.hideDropdown()
    },
    setDateTime (newVal) {
      var tmp = {}
      tmp.type = newVal.type
      tmp.value = newVal.value
      this.setHistory(tmp.type, tmp.value)
      // this.dateTime = tmp
      this.$emit('update:dateTime', tmp)
      tmp = null
    },
    clickCustom (event) {
      this.setCalendarData()
      this.showCalendar()
    },
    showCalendar () {
      this.calendarState = true
      this.recentState = false
    },
    clickRecent () {
      if (this.useRecent && this.recentList && this.recentList.length > 0) {
        this.showRecent()
      }
    },
    showRecent () {
      this.recentState = true
      this.calendarState = false
    },
    clickDropdown (event) {
      this.hideTimeDropdown('left')
      this.hideTimeDropdown('right')
    },
    clickInput (direct, event) {
      this.hideTimeDropdown(this.getOppositeDir(direct))
      if (this[`${direct}TimeDropdownState`]) {
        this.hideTimeDropdown(direct)
      } else {
        this.showTimeDropdown(direct, event)
      }
    },
    getOppositeDir (direct) {
      return direct === 'left' ? 'right' : 'left'
    },
    clickConfirm () {
      // 对于 对象 而言，修改其 key 的值，可以直接达到双向绑定的效果（父元素模板也会更新），如此修改只为统一修改入口
      // this.dateTime.type = this.custom.params.type
      // this.dateTime.value = []
      // // absolute（绝对时间）时，取整分钟
      // this.dateTime.value[0] = this.leftDate.seconds(0).unix()
      // this.dateTime.value[1] = this.rightDate.seconds(0).unix()
      this.setDateTime({
        type: this.custom.params.type,
        value: [this.leftDate.seconds(0).unix(), this.rightDate.seconds(0).unix()]
      })
      if (!this.useSelections) {
        // this.dateTimeRange = [this.leftDate.unix(), this.rightDate.unix()]
        this.$emit('update:dateTimeRange', [this.leftDate.unix(), this.rightDate.unix()])
      }
      // this.setHistory(this.dateTime.type, this.dateTime.value.slice())
      this.setHistory(this.custom.params.type, [this.leftDate.seconds(0).unix(), this.rightDate.seconds(0).unix()])
      this.hideDropdown()
    },
    focusInput (direct, start, end) {
      this.$refs[`${direct}Input`].setSelectionRange(start, end)
    },
    popInputSelectionToView (direct, arr, targetItem, activeClassName) {
      var tmp = []
      var keys = []
      var _className = activeClassName || 'active'
      for (var i = 0; i < arr.length; i++) {
        keys[i] = this.firstLetterToUpperCase(arr[i])
        tmp[i] = targetItem ? targetItem.offsetTop : this.$refs[`${direct}${keys[i]}`].getElementsByClassName(_className)[0].offsetTop
      }
      for (var k = 0; k < keys.length; k++) {
        this.$refs[`${direct}${keys[k]}`].scrollTop = tmp[k]
      }
      tmp = null
      keys = null
    },
    clickInputItem (direct, method, param, event) {
      var tmp = this[`${direct}Date`].clone()
      var _srcElement = event ? event.srcElement : undefined
      tmp[method](param)
      this[`${direct}Date`] = tmp
      tmp = null
      this.popInputSelectionToView(direct, [method], _srcElement, this.getTimeItemClass(direct, method, param))
      this.fixInputData(direct)
    },
    fixInputData (direct) {
      if (!this.leftDate.isBefore(this.rightDate, 'minute')) {
        var opp = direct === 'left' ? 'right' : 'left'
        var method = direct === 'left' ? 'add' : 'subtract'

        var tmp = this[`${direct}Date`].clone()
        tmp[method](1, 'minute')
        this[`${opp}Date`] = tmp

        this.$nextTick(function () {
          this.popInputSelectionToView(opp, ['hour', 'minute'])
        })
        tmp = null
      }
    },
    changeMonth (direct, method, param, event) {
      this.hideTimeDropdown('left')
      this.hideTimeDropdown('right')
      var tmp = this.getCalendarMoment(direct)[method](param, 'month')
      this.setCalendarViewData(tmp, direct, true)
      tmp = null
    },
    changeDate (direct, param, event, fixMethod) {
      this.hideTimeDropdown('left')
      this.hideTimeDropdown('right')
      var tmp = null
      if (fixMethod) {
        tmp = this.getCalendarMoment(direct)[fixMethod](1, 'month')
      } else {
        tmp = this.getCalendarMoment(direct)
      }
      tmp.date(param).hour(this[`${direct}Date`].hour()).minute(this[`${direct}Date`].minute())
      this[`${direct}Date`] = tmp
      this.fixCalendarData(direct)
      this.setCalendarViewData(tmp, direct)
      tmp = null
    },
    setCalendarData (reset) {
      this.leftDate = (!reset && this.leftDate) || this.getNowRange(this.dateTime)[0]
      this.rightDate = (!reset && this.rightDate) || this.getNowRange(this.dateTime)[1]
      this.setCalendarViewData(this.leftDate, 'left', true)
      this.setCalendarViewData(this.rightDate, 'right')
    },
    fixCalendarData (direct) {
      if (this.leftDate.isAfter(this.rightDate, 'day')) {
        if (direct === 'left') {
          this.setTo('rightDate', 'leftDate')
        } else {
          this.setTo('leftDate', 'rightDate')
        }
      }
      this.fixInputData(direct)
    },
    setTo (to, from) {
      var tmp = this[to].clone()
      tmp.set({
        'year': this[from].year(),
        'month': this[from].month(),
        'date': this[from].date()
      })
      this[to] = tmp
      tmp = null
    },
    setCalendarViewData (date, direct, notCheck) {
      this[`${direct}Year`] = date.year()
      this[`${direct}Month`] = date.month()
      if (!notCheck) {
        if (direct === 'left') {
          this.fixCalendarViewData('right')
        } else {
          this.fixCalendarViewData('left')
        }
      }
    },
    fixCalendarViewData (direct) {
      var tmpL = this.getCalendarMoment('left')
      var tmpR = this.getCalendarMoment('right')
      var source = direct === 'left' ? this.getCalendarMoment('right') : this.getCalendarMoment('left')
      if (tmpL.isAfter(tmpR)) {
        this.setCalendarViewData(source, direct, true)
      }
      tmpL = null
      tmpR = null
      source = null
    },
    calendarItemClass (dateNum, direct) {
      var tmp = this.getCalendarMoment(direct).date(dateNum)
      var opposite = direct === 'left' ? 'right' : 'left'
      return {
        'active': tmp.isSame(this[`${direct}Date`], 'day'),
        'in-range': tmp.isBetween(this.leftDate, this.rightDate, 'day') || tmp.isSame(this[`${opposite}Date`], 'day')
      }
    },
    calculateCalendar (year, month) {
      var date = moment().year(year).month(month)
      var firstDate = 1
      var lastDate = date.daysInMonth()
      var firstDay = date.set('date', firstDate).day()
      var lastDay = date.set('date', lastDate).day()
      var lastMonth = date.subtract(1, 'month').daysInMonth()
      date = null
      if (firstDay === 0) {
        firstDay = 7
      }
      if (lastDay === 6) {
        lastDay = -1
      }
      return [
        {
          arr: firstDay,
          fix: lastMonth - firstDay + 1
        },
        {
          arr: lastDate,
          fix: 1
        },
        {
          arr: 7 - lastDay - 1,
          fix: 1
        }
      ]
    },
    isInputItemActive (direct, method, param) {
      return this[`${direct}Date`][method]() === param
    },
    firstLetterToUpperCase (str) {
      var tmp = str.charAt(0)
      return str.replace(tmp, tmp.toUpperCase())
    },
    toggleDropdown (event) {
      if (this.dropdownState) {
        this.hideDropdown(event)
      } else {
        this.showDropdown(event)
      }
    },
    showDropdown (event) {
      this.dropdownState = true
      this.dropdownFlag = true
      this.addDocEvent()
      if (this.dateTime.type === this.custom.params.type || !this.useSelections) {
        // this.calendarState = true
        if (!this.calendarState) {
          this.clickCustom()
        }
      } else {
        this.calendarState = false
      }
    },
    hideDropdown (event) {
      this.hideTimeDropdown('left')
      this.hideTimeDropdown('right')
      this.dropdownState = false
      this.clearDocEvent()
    },
    showTimeDropdown (direct, event) {
      this.popInputSelectionToView(direct, ['hour', 'minute'])
      this.focusInput(direct, 0, 2)
      this[`${direct}TimeDropdownState`] = true
    },
    hideTimeDropdown (direct, event) {
      this[`${direct}TimeDropdownState`] = false
    },
    docEventHideDropDown () {
      if (!this.dropdownFlag) {
        this.hideDropdown()
      } else {
        this.dropdownFlag = false
      }
    },
    addDocEvent () {
      document.addEventListener('click', this.docEventHideDropDown)
    },
    clearDocEvent () {
      document.removeEventListener('click', this.docEventHideDropDown)
    },
    verifyLocalDateTime (strList, objList) {
      if (!strList || !objList || !isArray(strList) || !isArray(objList)) {
        return false
      }
      for (var i = 0; i < objList.length; i++) {
        if (isArray(objList[i].value)) {
          if (objList[i].value.join() !== strList[i]) {
            return false
          }
        } else {
          return false
        }
      }
      return true
    },
    getRecentFromLocal () {
      if (this.useRecent) {
        try {
          if (!localStore.isFake() && localStore(this.dateTimeRecentKey)) {
            var _data = localStore(this.dateTimeRecentKey)
            if (this.verifyLocalDateTime(_data.str, _data.obj)) {
              this.recentStrList = _data.str
              this.recentList = _data.obj
            }
          }
        } catch (e) {
          this.logTheError(e)
        }
      }
    },
    saveRecentToLocal () {
      if (this.useRecent) {
        try {
          if (!localStore.isFake()) {
            localStore(this.dateTimeRecentKey, {
              str: this.recentStrList,
              obj: this.recentList
            })
          }
        } catch (e) {
          this.logTheError(e)
        }
      }
    },
    setRecent (type, value) {
      if (this.useRecent && type === this.custom.params.type) {
        var _index = this.recentStrList.indexOf(value.join())
        var newTimeObj = {
          type: type,
          value: value
        }
        if (_index > 0) {
          this.recentStrList.splice(_index, 1)
          this.recentList.splice(_index, 1)
        }
        if (_index !== 0) {
          this.recentList.unshift(newTimeObj)
          this.recentStrList.unshift(value.join())
        }
        if (this.recentList.length > this.recentLength) {
          this.recentList.pop()
          this.recentStrList.pop()
        }
        this.saveRecentToLocal()
      }
    },
    setHistory (type, value) {
      this.setRecent(type, value)
      if (this.historyList.length >= this.historyLength) {
        this.historyList.pop()
      }
      var newTimeObj = {
        type: type,
        value: value
      }
      this.historyCurrentIndex = 0
      this.historyList.unshift(newTimeObj)
    },
    useHistory (type, value) {
      var _value = isArray(value) ? value.slice() : value
      // this.dateTime.type = type
      // this.dateTime.value = _value
      this.setDateTime({
        type: type,
        value: _value
      })
    },
    go (index, maintainList) {
      if (index >= this.historyList.length || index < 0) {
        return {
          type: 'error',
          value: 'illegal index'
        }
      } else {
        var tmp = this.historyList[index]
        if (!maintainList) {
          this.historyList.$remove(tmp)
          this.historyList.$set(0, tmp)
        }
        this.useHistory(tmp.type, tmp.value)
        return {
          type: tmp.type,
          value: tmp.value
        }
      }
    },
    prev () {
      this.historyCurrentIndex++
      if (this.historyCurrentIndex >= this.historyList.length) {
        this.historyCurrentIndex = this.historyList.length - 1
        return {
          done: true
        }
      } else {
        return this.go(this.historyCurrentIndex, true)
      }
    },
    next () {
      this.historyCurrentIndex--
      if (this.historyCurrentIndex < 0) {
        this.historyCurrentIndex = 0
        return {
          done: true
        }
      } else {
        return this.go(this.historyCurrentIndex, true)
      }
    },
    initDateTimeRangeMode () {
      this.setDateTimeRangeModeData()
      this.setDateTimeRangeModeWatcher()
    },
    setDateTimeRangeModeData () {
      if (this.dateTimeRange.length === 2) {
        this.checkValue(this.dateTimeRange)
        // this.dateTime.type = this.custom.params.type
        // this.dateTime.value = []
        // this.dateTime.value[0] = this.dateTimeRange[0]
        // this.dateTime.value[1] = this.dateTimeRange[1]
        this.setDateTime({
          type: this.custom.params.type,
          value: [this.dateTimeRange[0], this.dateTimeRange[1]]
        })
      }
    },
    setDateTimeRangeModeWatcher () {
      this.$watch('dateTimeRange', (val, oldVal) => {
        this.setDateTimeRangeModeData()
        this.setCalendarData(true)
      })
    }
  },
  filters: {
    placeholder (value) {
      return value < 10 ? `0${value}` : value
    }
  }
}
</script>

