<template>
  <div class="v-time-slider-container handle-label-top">
    <div class="v-timeline-content">
      <slot></slot>
    </div>
    <div class="v-timeline-holder" :style="sliderStyle" :class="{ 'unclickable': !clickMove }">
      <v-slider-base :class=[handlerTheme] :immediately="true" :min="min" :max="max" :value.sync="sliderValue" @on-change="onSliderChange" @on-input="onSliderInput">
        <div slot="handle" class="ui-slider-handle v-timeline-handle">
          <div class="v-timeline-handle-inner"></div>
        </div>
      </v-slider-base>
    </div>
  </div>
</template>
<script>
import moment from 'moment'
import vSliderBase from '../vSliderBase/VSliderBase'

export default {
  name: 'vTimeSlider',
  data () {
    return {
      sliderValue: 0,
      selectedMoment: null,
      selectedTimeRange: [],
      sendEventTimer: null,
      sendEventDelay: 500
    }
  },
  props: {
    interval: {
      required: false,
      type: Number,
      default: 60
    },
    // currentTime 为时间轴的结束时间段的开始时间点，比如 23:00 - 24:00 的轴，interval 为 60 秒
    // 最后一个时间段为 23:59:00.000 - 24:00:00.000，此时 currentTime 为 23:59:00.000，而非 24:00:00.000
    // 这样做的原因是：若选择使用结束时间点，上例中的 24:00:00.000 已经是第二天了，这将导致日期不准确
    // currentTimeUnix 就是 currentTime 的 unix 时间戳
    currentTimeUnix: {
      required: false,
      type: Number,
      default () {
        return moment().unix()
      }
    },
    steps: {
      required: false,
      type: Number,
      default: 60
    },
    locale: {
      required: false,
      type: String,
      default: 'zh-cn'
    },
    clickMove: {
      required: false,
      type: Boolean,
      default: true
    },
    handlerTheme: {
      required: false,
      type: String,
      default: 'one-line-theme',
      validator (value) {
        return ['default-theme', 'circle-theme', 'one-circle circle-theme', 'one-line-theme'].indexOf(value) !== -1
      }
    }
  },
  components: {
    vSliderBase
  },
  created () {
    moment.locale(this.locale)
  },
  mounted () {
    this.calcLabels()
  },
  beforeDestroy () {
    this.clearSendEventTimer()
  },
  computed: {
    min () {
      return 1 - this.steps
    },
    max () {
      return 0
    },
    sliderStyle () {
      var halfInterval = (100 / this.steps / 2).toFixed(4) + '%'
      return {
        left: halfInterval,
        right: halfInterval
      }
    }
  },
  methods: {
    calcSelectedMoment (delta) {
      if (!this.selectedMoment || delta === undefined) {
        this.resetMoment()
      }
      if (delta !== undefined) {
        var seconds = delta * this.interval
        this.selectedMoment.add(seconds, 'seconds')
      }
    },
    calcLabels (delta) {
      this.calcSelectedMoment(delta)
      this.selectedTimeRange = [this.selectedMoment.unix()]
      this.selectedMoment.add(this.interval, 'seconds')
      this.selectedTimeRange.push(this.selectedMoment.unix())
      this.selectedMoment.subtract(this.interval, 'seconds')
    },
    resetMoment () {
      this.selectedMoment = moment.unix(this.currentTimeUnix).second(0).millisecond(0)
    },
    moveByStep (step) {
      let tmp = this.sliderValue
      this.calcLabels(step)
      this.setSliderValue(tmp + step)
    },
    setSliderValue (newValue) {
      if (newValue <= this.max && newValue >= this.min) {
        this.sliderValue = newValue
        this.sendChangeEvent(newValue)
      }
    },
    sendChangeEvent (newValue) {
      this.setSendEventTimer(newValue)
    },
    setSendEventTimer (value) {
      this.clearSendEventTimer()
      this.sendEventTimer = setTimeout(this.onSliderChange, this.sendEventDelay, value)
    },
    clearSendEventTimer () {
      if (this.sendEventTimer) {
        clearTimeout(this.sendEventTimer)
        this.sendEventTimer = null
      }
    },
    onSliderChange () {
      this.sendOnChange()
    },
    onSliderInput (val, oldVal) {
      this.calcLabels(val - oldVal)
      this.sendOnInput()
    },
    sendOnChange () {
      this.$emit('on-change', this.selectedTimeRange)
    },
    sendOnInput () {
      this.$emit('on-input', this.selectedTimeRange)
    }
  },
  watch: {
    currentTimeUnix (val, oldVal) {
      this.sliderValue = this.max
      this.calcLabels()
      this.sendOnChange()
      this.sendOnInput()
    }
  }
}
</script>

<style src="./vTimeline.less" lang="less"></style>



