<template>
  <div class="placeholder-container flex-column flex-center" :class="{ 'placeholder-load-fail': type === 'error' || type === 'noData'}">
    <div class="loadFail-container-text flex-center" :class="[type === 'loading' && loadIcon ? 'flex-row': 'flex-column']">
      <div class='placeholder-img' :class="[{'monochrome': monochrome, 'rotation-img': loadIcon}, type]">
      </div>
      <div class='placeholder-tip'>
        <span>{{ msgTxt }}</span>
        <loading-icon v-if="type === 'loading' && !loadIcon"></loading-icon>
      </div>
    </div>
    <div class="flex-center placeholder-btn" v-if="type === 'error' || type === 'notFound'">
      <slot></slot>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    type: {
      validator (value) {
        return ['loading', 'error', 'noData', 'noAccess', 'notFound', 'noMatch'].indexOf(value) !== -1
      },
      default: 'loading'
    },
    loadIcon: {
      type: Boolean,
      default: false
    },
    msg: {
      type: String,
      default: ''
    },
    monochrome: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      defaultMsg: {
        loading: this.$t('text.loadingMsg'),
        error: this.$t('text.errorMsg'),
        noData: this.$t('text.noDataMsg'),
        noAccess: this.$t('text.noAccessMsg'),
        notFound: this.$t('text.notFoundMsg'),
        noMatch: this.$t('text.noMatchMsg')
      }
    }
  },
  computed: {
    msgTxt () {
      return this.msg || this.defaultMsg[this.type] || ''
    }
  }
}
</script>

<style src="./placeholder.less" lang="less"></style>



