<template>
  <v-slider-base class="v-slider" :disabled="disabled" :value.sync="baseValue" :values.sync="baseValues" :vertical="vertical" :range="range" :step="step" :min="min" :max="max" :fixed-to="fixedTo" :immediately="true" @on-input="onInput" @on-change="onChange">
    <div slot="handle" slot-scope="props" class="ui-slider-handle">
      <div class="handle-button"></div>
      <div class="handle-tooltip" :class="['tooltip-' + tooltipPosition, !tooltip ? 'hidden-tooltip' : '']">{{ props.value }}</div>
    </div>
  </v-slider-base>
</template>
<script>
import vSliderBase from '../vSliderBase/VSliderBase'

export default {
  name: 'vSlider',
  props: {
    disabled: {
      required: false,
      type: Boolean,
      default: false
    },
    vertical: {
      required: false,
      type: Boolean,
      default: false
    },
    range: {
      required: false,
      type: Boolean,
      default: false
    },
    step: {
      required: false,
      type: Number,
      default: 1
    },
    min: {
      required: false,
      type: Number,
      default: 0
    },
    max: {
      required: false,
      type: Number,
      default: 100
    },
    fixedTo: {
      required: false,
      type: String,
      default: '',
      validator (value) {
        return value === '' || value === 'min' || value === 'max'
      }
    },
    value: {
      required: false,
      type: Number,
      twoWay: true,
      default: 0
    },
    values: {
      required: false,
      type: Array,
      twoWay: true,
      default () {
        return []
      }
    },
    tooltip: {
      required: false,
      type: Boolean,
      default: true
    },
    tooltipPosition: {
      required: false,
      type: String,
      default: 'top',
      validator (value) {
        return value === 'top' || value === 'bottom' || value === 'left' || value === 'right'
      }
    }
  },
  computed: {
    baseValue: {
      get () {
        return this.value
      },
      set (val) {
        this.$emit('update:value', val)
      }
    },
    baseValues: {
      get () {
        return this.values
      },
      set (val) {
        this.$emit('update:values', val)
      }
    }
  },
  data () {
    return {
    }
  },
  components: {
    vSliderBase
  },
  compiled () {
  },
  mounted () {
  },
  methods: {
    onInput (value) {
      this.sendEvent('on-input', value)
    },
    onChange (value) {
      this.sendEvent('on-change', value)
    },
    sendEvent (eventName, value) {
      this.$emit(eventName, value)
    }
  },
  watch: {
  }
}
</script>

<style src="./vSlider.less" lang="less"></style>



