import moment from 'moment'

const calculateMonthCalendar = function (year) {
  var date = moment().year(year).dayOfYear(1)
  var monthArr = []
  for (var month = 0; month < 12; month++) {
    monthArr.push(date.month(month).daysInMonth())
  }
  return monthArr
}

// return the formated data with all keys
export const formatcalendarDataALL = function (calendarData, stateMap) {
  stateMap = stateMap || {
    0: 0,
    1: 1
  }
  var formated = {}
  for (var year in calendarData) {
    formated[year] = {}
    var monthOfYear = calculateMonthCalendar(year)
    var dayIndex = 0
    for (var month = 0; month < monthOfYear.length; month++) {
      formated[year][month] = {}
      for (var date = 1; date <= monthOfYear[month]; date++) {
        var value = stateMap[calendarData[year][dayIndex]] || 0
        formated[year][month][date] = value
        dayIndex++
      }
    }
  }
  return formated
}

// return formated data without empty keys
export const formatcalendarData = function (calendarData, stateMap) {
  stateMap = stateMap || {
    0: 0,
    1: 1
  }
  var formated = {}
  for (var year in calendarData) {
    formated[year] = {}
    var monthOfYear = calculateMonthCalendar(year)
    var daysInMonths = monthOfYear[0]
    var monthIndex = 0
    for (var dayIndex = 0; dayIndex < calendarData[year].length; dayIndex++) {
      if (dayIndex >= daysInMonths) {
        monthIndex++
        daysInMonths += monthOfYear[monthIndex]
      }
      if (stateMap[calendarData[year][dayIndex]]) {
        formated[year][monthIndex] = formated[year][monthIndex] || {}
        var date = dayIndex + 1 - daysInMonths + monthOfYear[monthIndex]
        formated[year][monthIndex][date] = 1
      }
    }
    if (Object.keys(formated[year]).length === 0) {
      // 设置为 undefined 比 delete 这个 key 更为高效，但是为了避免后台的修改，暂时不错这个优化
      delete formated[year]
    }
  }
  return formated
}

export const unformatcalendarData = function (calendarData, stateMap) {
  stateMap = stateMap || {
    0: 0,
    1: 1
  }
  var unformated = {}
  for (var year in calendarData) {
    unformated[year] = []
    var monthOfYear = calculateMonthCalendar(year)
    for (var month in calendarData[year]) {
      for (var date in calendarData[year][month]) {
        if (calendarData[year][month][date]) {
          date -= 0
          var dayIndex = monthOfYear.slice(0, month).reduce((sum, value) => sum + value, 0) + date - 1
          unformated[year][dayIndex] = stateMap[1]
        }
      }
    }
  }
  return unformated
}



