<template>
  <div id="app">
    <!--<router-view class="page-header-container" name="header"></router-view>-->
    <div class="page-body-container">
      <router-view name="body"></router-view>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'app',
  beforeCreate () {
  },
  created () {
    window.addEventListener('resize', this.changePageSize)
  },
  destroyed () {
    window.removeEventListener('resize', this.changePageSize)
  },
  methods: {
    ...mapActions([
      'changePageSize'
    ])
  }
}
</script>

<style src="./lib/app.less" lang="less"></style>



