<template>
<div class="v-slider-outer" :class="[disabled ? 'disabled' : 'enabled', { 'on-drag': changing }]">
  <div class="v-slider-base" ref="slider">
    <slot name="handle" v-for="item in handleValues" :value="item"></slot>
  </div>
</div>
</template>
<script>
import $ from 'jquery'
import 'jquery-ui/ui/widgets/slider'

export default {
  name: 'vSliderBase',
  props: {
    vertical: {
      required: false,
      type: Boolean,
      default: false
    },
    range: {
      required: false,
      type: Boolean,
      default: false
    },
    step: {
      required: false,
      type: Number,
      default: 1
    },
    min: {
      required: false,
      type: Number,
      default: 0
    },
    max: {
      required: false,
      type: Number,
      default: 100
    },
    fixedTo: {
      required: false,
      type: String,
      default: '',
      validator (value) {
        return value === '' || value === 'min' || value === 'max'
      }
    },
    reachableMin: {
      required: false,
      type: Number,
      default: NaN
    },
    reachableMax: {
      required: false,
      type: Number,
      default: NaN
    },
    value: {
      required: false,
      type: Number,
      twoWay: true,
      default: 0
    },
    values: {
      required: false,
      type: Array,
      twoWay: true,
      default () {
        return []
      }
    },
    immediately: {
      required: false,
      type: Boolean,
      default: false
    },
    disabled: {
      required: false,
      type: Boolean,
      default: false
    }
  },
  computed: {
    needVerifyMin () {
      return this.realReachableMin !== this.min
    },
    needVerifyMax () {
      return this.realReachableMax !== this.max
    },
    realReachableMin () {
      if (isNaN(this.reachableMin)) {
        return this.min
      } else {
        return this.reachableMin < this.min ? this.min : this.reachableMin
      }
    },
    realReachableMax () {
      if (isNaN(this.reachableMax)) {
        return this.max
      } else {
        return this.reachableMax > this.max ? this.max : this.reachableMax
      }
    },
    valueKey () {
      return this.range ? 'values' : 'value'
    },
    orientation () {
      return this.vertical ? 'vertical' : 'horizontal'
    },
    handleValues () {
      return this.range ? this.values : [this.value]
    }
  },
  data () {
    return {
      slider: null,
      changing: false,
      lastValue: null,
      sliderOptions: null
    }
  },
  components: {
  },
  compiled () {
  },
  mounted () {
    if (!this.isValid(this[this.valueKey])) {
      this.initValue()
    }
    this.resetSlider()
  },
  methods: {
    resetSlider () {
      this.sliderOptions = this.getSliderOptions()
      if (this.slider === null) {
        this.initSlider()
      } else {
        this.setSliderOption()
      }
    },
    initSlider () {
      this.slider = $(this.$refs.slider)
      this.slider.slider(this.sliderOptions)
    },
    getSliderOptions () {
      var self = this
      var options = {
        min: this.min,
        max: this.max,
        range: (!this.range && this.fixedTo) || this.range,
        orientation: this.orientation,
        step: this.step,
        slide: function (event, ui) {
          if (!self.isValid(ui[self.valueKey]) || self.disabled) {
            return false
          }
          if (self.immediately) {
            self.setValue(ui[self.valueKey])
          }
          self.sendEvent(ui[self.valueKey], 'on-input')
        },
        change: function (event, ui) {
          if (self.changing && !self.immediately) {
            self.setValue(ui[self.valueKey])
          }
          if (self.changing && self.isChanged(ui[self.valueKey])) {
            self.sendEvent(ui[self.valueKey], 'on-change')
          }
          self.changing = false
        },
        start: function (event, ui) {
          self.changing = true
          // 考虑可能的性能问题，记录 slider lastValue 并未在 setValue 方法中，而是放在了此处，故在手动修改 value 时没有记录
          self.setLastValue(ui[self.valueKey])
        }
      }
      options[this.valueKey] = this[this.valueKey]
      return options
    },
    setLastValue (value) {
      // lastValue 只是通过 slider 操作时的上次值，手动修改并不会触发记录
      // 对 value 的手动修改和 slider 修改，都可以在 value 超出规定范围前阻止 value 的变化，所以无需记录所有情况下的 lastValue（即不需要还原 value 值）
      this.lastValue = this.cloneVaule(value)
    },
    cloneVaule (value) {
      if (this.valueKey === 'values') {
        return [].concat(value)
      } else {
        return value
      }
    },
    isChanged (value) {
      if (this.valueKey === 'values') {
        return this.lastValue[0] !== value[0] || this.lastValue[1] !== value[1]
      } else {
        return this.lastValue !== value
      }
    },
    setValue (value, eventName) {
      // this[this.valueKey] = value
      this.$emit('update:' + this.valueKey, value)
    },
    sendEvent (value, eventName) {
      if (eventName) {
        this.$emit(eventName, value)
      }
    },
    setSliderOption () {
      this.slider.slider('option', this.sliderOptions)
    },
    setOuterRange () {
      this.slider.slider('option', {
        min: this.min,
        max: this.max
      })
    },
    isValid (newValue) {
      if (!this.needVerifyMin && !this.needVerifyMax) {
        return true
      } else {
        if (this.range) {
          return !isNaN(newValue[1]) && !isNaN(newValue[0]) && newValue[0] >= this.realReachableMin && newValue[0] <= newValue[1] && newValue[1] <= this.realReachableMax
        } else {
          return !isNaN(newValue) && newValue <= this.realReachableMax && newValue >= this.realReachableMin
        }
      }
    },
    initValue () {
      if (this.range) {
        var _tmp = (this.realReachableMin + this.step) > this.realReachableMax ? this.realReachableMax : this.realReachableMin + this.step
        this.values = [this.realReachableMin, _tmp]
      } else {
        this.value = this.realReachableMin
      }
    }
  },
  watch: {
    value (val, oldVal) {
      if (!this.changing && !this.range) {
        if (this.isValid(val)) {
          this.slider.slider('value', val)
        } else {
          console && console.error('VSlider Value unvalid')
        }
      }
    },
    values (vals, oldVals) {
      if (!this.changing && this.range) {
        if (this.isValid(vals)) {
          this.slider.slider('values', vals)
        } else {
          console && console.error('VSlider Value unvalid')
        }
      }
    },
    min (val, oldVal) {
      this.setOuterRange()
    },
    max (val, oldVal) {
      this.setOuterRange()
    },
    range (val, oldVal) {
      this.resetSlider()
    }
  }
}
</script>

<style src="./vSliderBase.less" lang="less"></style>



