<template>
  <button class="v-button" :class="btnClasses" :disabled="disabled">
    <span v-show="realLoading" class="v-button-loading"><i class="v-button-loading-icon icon-spinner2"></i></span><slot></slot>
  </button>
</template>

<script>
export default {
  props: {
    type: {
      required: false,
      type: String,
      default: 'default'
    },
    size: {
      required: false,
      type: String,
      default: 'default'
    },
    shape: {
      required: false,
      type: String,
      default: 'default'
    },
    icon: {
      required: false,
      type: Boolean,
      default: false
    },
    loading: {
      required: false,
      type: Boolean,
      default: false
    },
    miniLoadingTime: {
      required: false,
      type: Number,
      default: 0
    },
    disabled: {
      required: false,
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      sizeMap: {
        large: 'large',
        default: 'default',
        small: 'small',
        mini: 'mini'
      },
      typeMap: {
        default: '',
        ghost: 'ghost',
        text: 'text',
        primary: 'primary',
        info: 'info',
        success: 'success',
        error: 'error',
        warning: 'warning',
        light: 'light'
      },
      shapeMap: {
        default: '',
        circle: 'circle'
      },
      iconClassName: 'icon',
      loadingClassName: 'loading',
      attrs: [
        {
          map: 'sizeMap',
          prop: 'size',
          defaultClass: 'default'
        },
        {
          map: 'typeMap',
          prop: 'type',
          defaultClass: ''
        },
        {
          map: 'shapeMap',
          prop: 'shape',
          defaultClass: ''
        }
      ],
      innerLoading: false,
      innerLoadingTimer: null,
      loadingUnwatcher: null
    }
  },
  computed: {
    realLoading () {
      if (this.miniLoadingTime) {
        return this.loading || this.innerLoading
      } else {
        return this.loading
      }
    },
    propsClasses () {
      let classes = []
      for (let attr of this.attrs) {
        let className = this[attr.map][this[attr.prop]] || attr.defaultClass
        if (className) {
          classes.push(className)
        }
      }
      return this.icon ? classes.concat([this.iconClassName]) : classes
    },
    btnClasses () {
      return this.realLoading ? this.propsClasses.concat([this.loadingClassName]) : this.propsClasses
    }
  },
  created () {
    this.initLoadingWathcer()
  },
  destroyed () {
    this.removeLoadingWatcher()
    this.clearInnerLoadingTimer()
  },
  methods: {
    setLoadingWatcher () {
      if (!this.loadingUnwatcher) {
        this.loadingUnwatcher = this.$watch('loading', this.onLoadingChange)
      }
    },
    removeLoadingWatcher () {
      if (this.loadingUnwatcher) {
        this.loadingUnwatcher()
        this.loadingUnwatcher = null
      }
    },
    initLoadingWathcer () {
      if (this.miniLoadingTime) {
        this.setLoadingWatcher()
      } else {
        this.removeLoadingWatcher()
      }
    },
    onLoadingChange (val, oldVal) {
      if (val === true) {
        this.innerLoading = true
        this.setInnerLoadingTimer()
      }
    },
    clearInnerLoadingTimer () {
      if (this.innerLoadingTimer) {
        clearTimeout(this.innerLoadingTimer)
        this.innerLoadingTimer = null
      }
    },
    setInnerLoadingTimer () {
      this.clearInnerLoadingTimer()
      if (this.miniLoadingTime) {
        this.innerLoadingTimer = setTimeout(() => {
          this.innerLoading = false
        }, this.miniLoadingTime)
      }
    }
  },
  watch: {
    miniLoadingTime (val, oldVal) {
      this.initLoadingWathcer()
    }
  }
}
</script>
<style src="./vButton.less" lang="less"></style>



